<template>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span>权限列表</span>
        </div>

        <el-input
            placeholder="输入关键字进行过滤"
            v-model="filterText"
            style="margin-bottom: 20px">
        </el-input>

        <el-tree
        class="filter-tree"
        :data="data"
        :props="defaultProps"
        default-expand-all
        :filter-node-method="filterNode"
        ref="tree">
        </el-tree>
    </el-card>
</template>

<script>
  import {getAuthsListApi} from '@/api/auths.js'
  export default {
    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);
      }
    },

    methods: {
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      }
    },
    created() {
      getAuthsListApi().then(res => {
        console.log(res)
        res.data.forEach((item, index) => {
          this.data.push({label: '', children: []})
          this.data[index].label = item.auth_name
          item.children.forEach((child, i) => {
            this.data[index].children.push({label: ''})
            this.data[index].children[i].label = child.auth_name
          })
        })
      })
    },
    data() {
      return {
        filterText: '',
        data: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        }
      };
    }
  };
</script>